.blog {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 40px 20px;
}

.blog__title {
  margin: 0 0 20px;
  font-size: 2.2rem;
}

.blog__post-title {
  margin: 0 0 10px;
}

.blog__post-content {
  margin: 0 0 20px;
  text-align: justify;
  hyphens: auto;

  & + h2 {
    margin-top: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .blog {
    max-width: 800px;
    margin: 0 auto;
    padding: 50px 20px;
  }

  .blog__title {
    margin: 0 0 30px;
    font-size: 2.6rem;
  }
}
